          list_name_two: "点差低至",
          list_name_three: "杠杆高达2000倍"
        },
        {
          src: require("../assets/img/icon-anm-gold.svg"),
          alt: "黄金、白银",
          title_name: "黄金、白银",
          list_name_one: "行业领先点差低至0",
          list_name_two: "杠杆高达200倍",
          list_name_three: "隔夜费低至0.7美元"
        },
        {
          src: require("../assets/img/icon-anm-graph.svg"),
          alt: "股票CFD",
