          list_name_one: "美股港股齐备",
          list_name_two: "美股有苹果、特斯拉等",
          list_name_three: "港股有腾讯、比亚迪等"
        },
        {
          src: require("../assets/img/icon-anm-oil.svg"),
          alt: "商品和指数CFD",
          title_name: "商品和指数CFD",
          list_name_one: "A50点差低至1",
          list_name_two: "美油布油杠杆高达200倍",
          list_name_three: "标普道指杠杆高达500倍"
        },
        {
          src: require("../assets/img/icon-anm-pips.svg"),
          alt: "股票交易",
          title_name: "股票交易",
          list_name_one: "美股享有零佣金",
          list_name_two: "美股港股提供碎股交易",
          list_name_three: "免除平台费"
        }
      ],
      steps_boxs_items: [
        {
          src: require("../assets/img/step1.svg"),
          content: "开设MyFXTM账户"
        },
        {
