          background-color: #ff4521 !important;
          color: #fff !important;
          transition: 0.2s;
          border: 1px solid #ff4521 !important;
          border-radius: 32px;

          text-align: center;
        }
      }
      .level-1 {
        border-left: solid 8px transparent;
        &:hover {
          border-left: solid 8px #ff4521;
        }
      }
      .level-2 {
        width: 100%;
        margin-bottom: 5px;
        margin-top: 5px;
        padding: 0 15px;
        a {
          &:hover {
            background-color: rgba(255, 255, 255, 0.14);
            border-radius: 8px;
          }
        }
      }

      .hide1 {
        display: none;
      }
      .level-3 .navbar-mobile_arrow {
        display: none;
      }
      .showcdrp {
        display: block !important;
      }
      .nav-item-back-btn {
        background-color: rgba(255, 255, 255, 0.08);
        text-align: left;
        height: 70px;

        .navbar-mobile_item-link-back-btn {
          display: block;
          padding: 22px 32px;
          .navbar-mobile_arrow-icon {
            margin-bottom: 2.5px;
            transform: rotate(180deg);
          }
        }
      }
      .nav-item-title {
        box-sizing: border-box;
        border-left: solid 8px #ff4521;
        padding: 12px 25px 12px 22px;
        color: #fff;
        font-size: 1.25rem;
        font-weight: 700;
        height: 52px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-right: 10px;
      }
      a {
        text-decoration: none;
        color: #fff;
        position: relative;
      }
      svg {
        pointer-events: none;
      }
    }
    .navbar-nav {
      padding-left: 0;
      margin-bottom: 0;
      list-style: none;
    }
  }
  .navbarNavDropdown_hide {
    display: block !important;
  }
  .navbarNavDropdown_hide1 {
    display: none;
  }
}

